*{
  margin:0px;
  padding: 0px;
}
#cabeza{
  height: 150px;
  background: whitesmoke;    
}
#foto{      
  float: left;  
}
#cabeza h1{   
  margin-top: 10px; 
  margin-left: 30px;
  margin-right: 10px;
  font-size: 20px;  
  line-height: 50px;
  color: black;    
}
body{
  background: gray;
  color:white;
}
#cont {
  width: 1000px;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  background: blue;
}
#menu{
  margin-top: 5px;
  margin-bottom: 1px;
  height: 50px;
  line-height: 50px;
  background: skyblue;
  text-align: center;     
}
#conte{
  width: 600px;
  height: 500px;
  float: left;
  background: whitesmoke;
  margin-top: 10px;
  margin-left: 10px;
  margin-bottom: 10px;
  font-size: center;
}
#conte h2{    
  margin-left: 10px;
  margin-right: 10px;
  font-size: 40px;
  text-align: left;
  line-height: 70px;
  color: black;
}
#conte p{
  margin-left: 15px;
  margin-right: 15px;
  font-size: 25px;
  text-align: left;
  line-height: 29px;
  color: black;
}
aside{
  width: 360px;
  height: 500px;
  font-size: 20px;
  background: whitesmoke;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  float: right;
}
aside h3{
  margin-left: 5px;
  margin-right: 5px;
  font-size: 40px;
  text-align: left;
  line-height: 50px;
  color: black;
}
aside p{
  margin-left: 15px;
  margin-right: 15px;
  font-size: 25px;
  text-align: left;
  line-height: 40px;
  color: black;
}
#ingle{    
  display: block;
  margin:10px auto;
}
#links{ 
  height: 270px;
  background: skyblue;
  padding: 20px;
  clear: both;;
}
#links h1{
  margin-left: 10px;
  margin-right: 10px;
  font-size: 30px;
  text-align: center;
  line-height: 30px;
  color: black;
}
#links p{
  margin-left: 10px;
  margin-right: 10px;
  font-size: 30px;
  text-align: left;
  line-height: 70px;
  color: black;
}
#boton{
  margin-left: 20px;
  text-decoration: none;
  padding: 10px;
  font-weight: 00;
  font-size: 20px;
  color: #ffffff;
  background-color: black;
  border-radius: 6px;
  border: 5px solid red;
}
